body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root{
	height: 100%;
}
.App{
	overflow: auto;
	height: 100%;
}
.sideMenuCon{
	width: 70px;
	float: left;
	background: #596db9;
	height: 100%;
}
.contentCon{
	width: calc(100% - 70px);
	float: left;
	height: 100%;
	overflow: auto;
	background: #edf2f6;
}

.sideMenu{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	height: 100%;
}
.sideMenu ul{
	margin: 0;
	list-style: none;
	padding: 0;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 100%;
}
.sideMenu ul li{
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
}
.sideMenu ul li a{
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	color: #fff;
	position: relative;
	font-size: 18px;
	text-decoration: none;
}
.sideMenu ul li a:after{
	position: absolute;
    left: -5px;
    height: calc(100% - 10px);
    top: 5px;
    width: 5px;
    background: #fff;
    -webkit-transition: all ease-out .3s 0s;
    transition: all ease-out .3s 0s;
    content: '';
}
.sideMenu ul li:hover a:after{
	left: 0;
}
.sideMenu ul li a span{
	position: absolute;
    padding: 10px;
    font-size: 14px;
    left: 0px;
    background: #2f323a;
    white-space: nowrap;
    color: #fff;
    border-radius: 3px;
    line-height: 1;
    top: 50%;
    margin-top: -25%;
    opacity: 0;
    pointer-events: none;
    -webkit-transition: all ease-out .3s 0s;
    transition: all ease-out .3s 0s;
    z-index: 50;
    text-transform: capitalize;
}
.sideMenu ul li a span:after{
	content: '';
    position: absolute;
    border-width: 5px;
    border-style: solid;
    border-color: transparent #2f323a transparent transparent;
    width: 0;
    height: 0;
    background: transparent;
    left: -10px;
    top: 0;
    bottom: 0;
    margin: auto;
}
.sideMenu ul li a:hover span{
	opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    left: 80px;
}

.sideMenu ul li a.active{
	background: #fff;
	color: #596db9;
}

